<template>
    <Layout>
        <Title name="Rechnung erstellen"/>
        <v-form ref="form">
            <v-container>
                <v-layout row justify-center>
                    <v-flex sm6 md3>
                        <v-select
                                :items="businesses"
                                label="Betriebe"
                                v-on:input="businessChanged"
                        ></v-select>
                    </v-flex>
                </v-layout>
                <v-layout row justify-center pb-4>
                    <v-btn
                            @click="generatePDF"
                            color="blue-grey"
                            dark>PDF erstellen
                    </v-btn>
                </v-layout>
                <iframe id='pdfV' style="width:100%; height: 500px; display: none"></iframe>
            </v-container>
        </v-form>
    </Layout>
</template>

<script>
    import Layout from "../utils/StandardLayout";
    import Title from "../utils/Title";
    import {getBusinesses} from "../utils/script/invoiceService";
    import {createpdf} from "../utils/script/rechnung";

    export default {
        name: "CreateInvoice",

        data() {
            return {
                businesses: [],
                businessId: "",
            }
        },
        components: {Title, Layout},
        methods: {
            businessChanged(businessids) {
                this.businessId = businessids;
            },
            generatePDF() {
                if (this.businessId !== "") {
                    createpdf(this.businessId);
                    document.getElementById('pdfV').style.display = 'block';
                } else {
                    alert("bitte Betrieb auswÃ¤hlen")
                }
            }
        },
        beforeMount() {
            this.businesses = getBusinesses();
        },

    }
</script>

